import del from '@/static/mbcImg/publish/createBusiness/del.png';
import replaceIcon from '@/components/common/RHX/replaceIcon/replaceIcon';
import inputBox from '@/components/common/RHX/inputBox/inputBox';
export default {
	components: {
		replaceIcon,
		inputBox
	},
	props: [
		'dataList',
		'itemsData',
		'indexNum',
		'isSelect',
		'disabled',
		'iconSrc',
		'iconWidth',
		'iconHeight',
		'iconTAlign',
		'iconPadding',
		'isShowOne',
		'valueOne',
